import React, { Fragment, useEffect, useState, useContext } from 'react';
import { Row, Col, Card, CardBody, Button, InputGroup, CustomInput } from 'reactstrap';
import FalconCardHeader from '../common/FalconCardHeader';
import axios from 'axios';
import PurchasesTableActions from '../../components/dashboard/PurchasesTableActions';
import AppContext from '../../context/Context';
import { applicantsApi } from '../../config';
import Loader from '../common/Loader';
import moment from 'moment';
import Select from '../common/Select';
import applicantFilters from '../../data/dashboard/applicantFilters';

import loadable from '@loadable/component';
const PurchasesTable = loadable(() => import('./PurchasesTable'));

const availableFilters = applicantFilters
  .slice(0, 10)
  .map((applicantFilter, index) => ({ value: index, label: applicantFilter }));

const Dashboard = () => {
  // State
  const [isSelected, setIsSelected] = useState(false);
  const [data, setData] = useState(null);
  const [originalData, setOriginalData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [filters, setFilters] = useState([]);

  // Context
  const { authToken } = useContext(AppContext);

  useEffect(() => {
      const fetchData = async () => {
        if (!authToken) {
          console.log("authToken missing in fetchData");
          return;
        }

        try {
          axios.defaults.headers.common = {
            'Authorization': 'Bearer ' + authToken
          };
  
          const url = `${applicantsApi}?page=1&per_page=1000`;
          const applicantsData = await axios.get(url);
          console.debug(">>> applicantsData.data >>> ", applicantsData.data);
          
          if (applicantsData.data && applicantsData.data.items) {
            console.log("items >>> ", applicantsData.data.items);
            const items = [];
            for (const dataItem of applicantsData.data.items) {
              const applicantStatus = (dataItem.applicant_status) ? dataItem.applicant_status : "";
              const ts = (dataItem.timestamp) ? dataItem.timestamp : "";
              const submittedOn = (ts) ? moment(ts).format() : moment.format();
              const submittedDate = (submittedOn) ? moment(submittedOn).calendar() : "";
              const item = {
                "id" : dataItem.id,
                "full_name": `${dataItem.first_name} ${dataItem.last_name}`,
                "submitted_on": submittedDate,
                "degree_program": dataItem.degree_program,
                "status": applicantStatus,
                "action": <PurchasesTableActions id={dataItem.id} />
              }
              items.push(item);
            }
            setData(items);
            setOriginalData(items);
          }
        } catch(err) {
          window.location.href="/authentication/basic/login";
        }
        setLoading(false);
      };
      fetchData();
  }, []);  

  const filterByStatus = (filterText) => {
    if (!data || data.length === 0) { return; }
    if (!filterText || filterText.length === 0) { return; }
    const idx = filterText.indexOf(' - ');
    if (idx == -1) { return; }
    const parsedText = filterText.substring(idx+2);
    console.log(">>> parseText >>> ", parsedText);
    const normalizedFilter = (parsedText) ? parsedText.trim().toLowerCase() : "";
    console.log(">>> normalizedFilter >>> ", normalizedFilter);
    let filteredData = [];
    for (const item of data) {
      if (item.status && item.status === normalizedFilter) {
        filteredData.push(item);
      }
    }
    if (filteredData && filteredData.length > 0) {
      setData(filteredData);
    }
  };

  const filterByDegree = (filterText) => {
    if (!data || data.length === 0) { return; }
    let filteredData = [];
    for (const item of data) {
      if (item.status && item.status === filterText) {
        filteredData.push(item);
      }
    }
    if (filteredData && filteredData.length > 0) {
      setData(filteredData);
    }
  };
  
  const filterByTimeline = (filterText) => {
    if (!data || data.length === 0) { return; }
    let filteredData = [];
    for (const item of data) {
      if (item.status && item.status === filterText) {
        filteredData.push(item);
      }
    }
    if (filteredData && filteredData.length > 0) {
      setData(filteredData);
    }
  };  

  const handleApplyFilters = e => {
    e.preventDefault();
    console.log(">>> handleApplyFilter >>> filters >>> ", filters);
    if (!filters || filters.length === 0) { return; }
    if (!data || data.length === 0) { return; }
    for (const filter of filters) {
      const filterLabel = (filter.label && filter.label.length > 0) ? filter.label : "";
      if (filterLabel.indexOf('Status') != -1) {
        filterByStatus(filterLabel);
      }
      if (filterLabel.indexOf('Degree') != -1) {
        filterByDegree(filterLabel);
      }
      if (filterLabel.indexOf('Timeline') != -1) {
        filterByTimeline(filterLabel);
      }      
    }
  };  

  const handleResetFilters = e => {
    e.preventDefault();
    console.log(">>> handleResetFilters");
    setFilters([]);
    setData(originalData);
  };    

  return (
    <Fragment>
      {loading ? (
        <Loader />
      ) : (      
      <Card className="mb-3">
        <FalconCardHeader title="Current Applicants" light={false}>
          {isSelected ? (
            <InputGroup size="sm" className="input-group input-group-sm">
              <CustomInput type="select" id="bulk-select">
                <option>Bulk actions</option>
                <option value="Accept">Accept Selected</option>
                <option value="Hold">Withdraw Selected</option>
              </CustomInput>
              <Button color="falcon-default" size="sm" className="ml-2">
                Apply
              </Button>
            </InputGroup>
          ) : (
            <Fragment>
              <Row>
                <Col>
                  <Select
                    style={{width:250}}
                    options={availableFilters}
                    placeholder="Filters"
                    value={filters}
                    onChange={value => setFilters(value)}
                    closeMenuOnSelect={false}
                    autoFocus
                    isMulti
                    isCreatable
                  />              
                  <Button color="falcon-default" size="sm" className="ml-2 mt-2" onClick={handleApplyFilters} >
                    Apply Filters
                  </Button>
                  <Button color="falcon-default" size="sm" className="ml-2 mt-2" onClick={handleResetFilters} >
                    Reset
                  </Button>                
                </Col>
              </Row>              
            </Fragment>
          )}
        </FalconCardHeader>
        {data ? (
          <CardBody className="p-0">
            <PurchasesTable setIsSelected={setIsSelected} purchases={data} />
          </CardBody>
        ) : <div />}
      </Card>
      )}
    </Fragment>
  );
};

export default Dashboard;
