import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import AppContext from './context/Context';
import toggleRTL from './helpers/toggleRTL';

const Main = props => {
  const parse = key => {
    if (key) {
      return JSON.parse(localStorage.getItem(key))
    }
  };

  const read = key => {
    if (key) {
      return localStorage.getItem(key)
    }
  };

  const setFluid = isFluid => {
    localStorage.setItem('isFluid', isFluid);
    window.location.reload();
  };

  const setRTL = isRTL => {
    setIsRTL(isRTL);
    localStorage.setItem('isRTL', isRTL);
    toggleRTL(isRTL);
  };

  const setDark = isDark => {
    setIsDark(isDark);
    localStorage.setItem('isDark', isDark);
  };

  const setLoggedIn = loggedIn => {
    setIsLoggedIn(loggedIn);
    localStorage.setItem('loggedIn', loggedIn);
  };

  const setAuthToken = authToken => {
    setUserAuthToken(authToken);
    if (authToken) {
      localStorage.setItem('authToken', authToken);
    }
  };

  // const setLastLoginTime = lastLoginTime => {
  //   setUserLastLoginTime(lastLoginTime);
  //   localStorage.setItem('lastLoginTime', lastLoginTime);
  // };  

  const [isFluid] = useState(parse('isFluid') || false);
  const [isRTL, setIsRTL] = useState(parse('isRTL') || false);
  const [isDark, setIsDark] = useState(parse('isDark') || false);
  const [showBurgerMenu, setShowBurgerMenu] = useState(false);
  const [currency, setCurrency] = useState('$');
  const [loggedIn, setIsLoggedIn] = useState(parse('loggedIn') || false);  
  const [authToken, setUserAuthToken] = useState(read('authToken') || ''); 
  // const [loggedIn, setIsLoggedIn] = useState(false);  
  // const [authToken, setUserAuthToken] = useState('');    
  const [me, setMe] = useState(null);  
  const [welcomeDisplayed, setWelcomeDisplayed] = useState(false);
  // const [lastLoginTime, setUserLastLoginTime] = useState(parse('lastLoginTime') || '');  

  useEffect(() => {
    isRTL && toggleRTL(isRTL);
  }, [isRTL]);

  return (
    <AppContext.Provider
      value={{
        isFluid,
        setFluid,
        isRTL,
        setRTL,
        isDark,
        setDark,
        showBurgerMenu,
        setShowBurgerMenu,
        currency,
        setCurrency,
        loggedIn,
        setLoggedIn,
        authToken, 
        setAuthToken,
        me,
        setMe,
        welcomeDisplayed, 
        setWelcomeDisplayed,
        // lastLoginTime,
        // setLastLoginTime
      }}
    >
      {props.children}
    </AppContext.Provider>
  );
};

Main.propTypes = { children: PropTypes.node };

export default Main;
