import React, { useContext, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Route, Switch, Redirect } from 'react-router-dom';
import Dashboard from '../components/dashboard/Dashboard';
import NavbarTop from '../components/navbar/NavbarTop';
import NavbarVertical from '../components/navbar/NavbarVertical';
import Footer from '../components/footer/Footer';
import loadable from '@loadable/component';
import { meApi } from '../config';
import AppContext from '../context/Context';
import ProductProvider from '../components/e-commerce/ProductProvider';
import axios from 'axios';
import withRedirect from '../hoc/withRedirect';

const AdminRoutes = loadable(() => import('./AdminRoutes'));
const DashboardRoutes = loadable(() => import('./DashboardRoutes'));

const DashboardLayout = ({ location, setRedirect, setRedirectUrl }) => {
  const { authToken } = useContext(AppContext);
  const { isFluid } = useContext(AppContext);
  const { loggedIn } = useContext(AppContext);
  const { me, setMe } = useContext(AppContext);

  useEffect(() => {
    const fetchMe = async () => {
      console.log(">>> ENTERED: [fetchMe]");
      if (!authToken) {
        console.log("authToken missing in [fetchMe]");
        return;
      }

      try {
        axios.defaults.headers.common = {
          'Authorization': 'Bearer ' + authToken
        };
  
        const url = `${meApi}`;
        const response = await axios.get(url);
        console.debug(">>> fetchMe >>> response.data >>> ", response.data);
        
        if (response.data) {
          setMe(response.data);
        }
  
      } catch(err) {
        // alert("Erropr")
        setRedirect(true);
      }
    };
    fetchMe();
  }, []);    

  useEffect(() => {
    AdminRoutes.preload();
    DashboardRoutes.preload();
  }, []);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location.pathname]);

  useEffect(() => {
    setRedirectUrl(`/authentication/basic/login`);
  }, [setRedirectUrl]);  

  if (!loggedIn) {
    return <Redirect to={`/authentication/basic/login`} />;
  }  

  if (me && me.account_type && me.account_type === "admin") {
    console.log("You are an Admin...");
    return (
      <div className={isFluid ? 'container-fluid' : 'container'}>
        <NavbarVertical />
        <ProductProvider>
          <div className="content">
            <NavbarTop />
            <Switch>
              <Route path="/" exact component={Dashboard} />
              <AdminRoutes />
            </Switch>
            <Footer />
          </div>
        </ProductProvider>
      </div>
    );
  } else {
    console.log("You are a Student...");
    return (
      <div className={isFluid ? 'container-fluid' : 'container'}>
        <NavbarVertical />
        <ProductProvider>
          <div className="content">
            <NavbarTop />
            <Switch>
              <Route path="/" exact component={Dashboard} />
              <DashboardRoutes />
            </Switch>
            <Footer />
          </div>
        </ProductProvider>
      </div>
    );

  }
};

DashboardLayout.propTypes = { 
  location: PropTypes.object.isRequired ,
  setRedirect: PropTypes.func.isRequired,
  setRedirectUrl: PropTypes.func
};

export default withRedirect(DashboardLayout);
// export default DashboardLayout;
